@import 'base/var';
@import 'base/common';
@import 'home/portfolio-detail';
@import 'home/anlasyt';
@import 'home/introduce';
@import 'home/news';
@import 'home/video';
@import './find-route.css';

@define-extend title {
  font-size: 26px;
  font-weight: bold;

  height: 90px;

  text-align: left;
}

/**************搜索模块*************/
.search {
  position: relative;

  width: 100%;
  height: 285px;
  margin: 0;
  padding: 0;

  background-color: #343434;
  .title-search {
    font-size: 38px;
    line-height: 114px;

    height: 114px;

    text-align: center;

    color: $white;
  }
  form {
    height: 100px;
    margin: auto;
    .center-search {
      position: relative;

      width: 770px;
      height: 80px;
      margin: auto;
      .btn-search {
        font-size: 30px;
        line-height: 80px;

        float: left;

        width: 100px;
        height: 80px;

        cursor: pointer;

        color: $white;
        border: none;
        border-radius: 0 10px 10px 0;
        background: #ff8300;
      }
      .input-search {
        font-size: 30px;

        float: left;

        width: 650px;
        height: 80px;
        margin: 0;
        padding: 0;
        padding-left: 20px;

        border: 0;
        border-radius: 10px 0 0 10px;
        outline: none;
      }
    }
  }
  .tips-search {
    font-size: 22px;

    text-align: center;

    color: #999;
  }
}
/***************************主体部分*************************************/
.body {
  text-align: center;
  /***************************顶部banner*************************************/
  .banner-container {
    position: relative;

    overflow: hidden;

    height: 640px;
    ul {
      height: 640px;
      li {
        position: absolute;
        z-index: 1;

        width: 100%;
        height: 100%;

        transition: all 1.5s linear;

        opacity: 0;
        a,
        img {
          position: relative;

          display: inline-block;

          width: 100%;
          height: 100%;
        }
      }
      .banner-current {
        z-index: 5;

        opacity: 1;
      }
    }
    .focus-index {
      position: absolute;
      z-index: 6;
      bottom: 34px;

      width: 100%;
      i {
        display: inline-block;

        width: 15px;
        height: 15px;
        margin: 0 8px;

        border-radius: 100px;
        background: #fbb387;
      }
      i.on {
        background: $white;
      }
    }
    .title-banner {
      .top-title-banner {
        font-size: 56px;

        color: #fff;
      }
      .bottom-title-banner {
        font-size: 40px;

        width: 570px;
        height: 110px;
        margin: 90px auto;

        color: #fff;
        .txt-left {
          float: left;
        }
        .txt-right {
          float: right;
        }
        .clear-both {
          clear: both;
        }
        .spacing {
          letter-spacing: 6px;
        }
      }
    }
    .title-banner-layout {
      position: absolute;
      top: 50%;
      left: 50%;

      width: 920px;
      height: 270px;
      margin-top: -135px;
      margin-left: -460px;
      p {
        margin: 0;
      }
    }
  }
  /**************投资方向 思路 ，师傅 圈子*************/
  .wrap {
    position: relative;

    height: 458px;

    border-bottom: 1px solid #d0d0d2;
    .page-center {
      overflow: hidden;

      height: 100%;

      word-break: break-all;
      .title {
        text-align: center;
      }
      .option {
        padding-top: 70px;
        a {
          display: block;

          width: 204px;
          height: 204px;
          margin: auto;
          img {
            display: block;

            width: 100%;
            height: 100%;
          }
        }
        .find-route,
        .find-master,
        .find-circle {
          width: 33.33333333%;
        }
      }
    }
  }
}
/******收索结果********/
.search-result {
  .search {
    background-image: resolve(bg_search.png);
    background-repeat: no-repeat;
    background-size: cover;
    form .center-search .btn-search {
      background: #343434;
    }
    .tips-search {
      font-size: 22px;

      color: #fff;
    }
  }
}
.validate-search {
  line-height: 50px;

  position: absolute;
  top: -55px;

  width: 200px;
  height: 50px;

  color: red;
  border-radius: 10px;
  background: #fff000;
}
